body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.h1,
.h2,
.h3,
.h4,
.h5 {
  margin: 10px 0 10px;
}
.text-align-center {
  text-align: center;
}
.custom-alert {
  cursor: pointer;
  margin: 0;
}
.marker {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.marker i {
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  color: red;
  font-weight: bolder;
}
.marker .h6 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(80%, -200%);
  color: red;
}

.coordinates button {
  margin-left: 10px;
}

.animation-arrow {
  margin-left: 10px;
  transition: transform 0.25s ease-in-out;
}
.animation-arrow.active {
  transform: rotateX(180deg);
}

.pointer-events-none {
  pointer-events: none;
}
